.clientTypology{
    background-color: color(dark);
    padding-top: s(132);
    padding-bottom: s(140);

    &__title{
        @extend .subtitle;
        color: color(white);
        text-align: center;
        margin-bottom: s(50);
    }

    &__list{
        display: flex;
        flex-direction: column;
    }

    &__item{
        padding: s(35/2) 0;
        color: color(sand);
        text-align: center;
        @extend .title80;
        text-decoration: none;
        position: relative;

        span{
            z-index: 2;
            position: relative;
        }

        &:hover,
        &:focus{
            .clientTypology__video,
            .clientTypology__image{
                opacity: 1;
                //transition: 200ms opacity linear;
                z-index: 1;
            }
        }

        &--link{
            span{
 
                &:before{
                    background: color(sand);
                    bottom: 1px;
                    content: "";
                    height: 3px;
                    left: 0;
                    position: absolute;
                    transform: scaleX(0);
                    transform-origin: right center;
                    transition: transform .5s easing(ease-in-out-quint);
                    width: 100%;
                    will-change: transform;
                }
            }

            &:hover,
            &:focus{
                span:before{
                    transform: scaleX(1);
                    transform-origin: left center;
                }
            }
        }
    }
    &__video{
        
    }

    &__video,
    &__image{
        opacity: 0;
        position: fixed;
        left: calc(50vw + 62px) ;
        top: 50vh;
        width: s(545);
        height: s(545);
        border-radius: s(545);
        margin-top: s(-545/2);
        opacity: 0;
        pointer-events: none;

        //top: 0;
        //transform: translate3d(0,var(--y,50vh),0);
        //transition: 1000ms transform easing(ease-out-quad);

        video,
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
            border-radius: 100%;
            opacity: 0.5;
        }

        &:before{
            @extend .photoNoise;
            border-radius: 100%;
        }

    }
}