.cursor{
    display: none;

    &__bg{
        position: fixed;
        left: 0;
        top: 0;
        width: s(36);
        height: s(36);
        margin-top: s(-36/2);
        margin-left: s(-36/2);
        z-index: 600;
        border-radius: 100%;
        background-color: color(yellow);
        mix-blend-mode: darken;
        opacity: 1;

        will-change: opacity, color, transform;
        transform: translate3d(var(--x,50vw),var(--y,50vh),0) scale(0.1);
        transform-origin: center center;
        pointer-events: none;
        user-select: none;


        &--screen{
            opacity: 0;
            mix-blend-mode: screen;
        }

        &--multiply{
            opacity: 0;
            mix-blend-mode: multiply;
        }

        &--lighten{
            opacity: 0;
            mix-blend-mode: lighten;
        }
    }


    &--active{
        display: block;
        .cursor__bg{
            transform: translate3d(var(--x,50vw),var(--y,50vh),0) scale(1);
            transition: opacity 400ms easing(ease-out-quint),
                        transform 400ms easing(ease-out-quint), 
                        background-color 400ms easing(ease-out-quint);
        }
    }

    &--focused{
        @extend .cursor--medium;
    }

    &--light{
        .cursor__bg{
            background-color: color(yellow);
            opacity: 0;
        }

        .cursor__bg--multiply{
            opacity: 1;
        }
    }

    &--dark{
        .cursor__bg{
            background-color: color(blue);
            opacity: 0;
        }
        .cursor__bg--lighten{
            opacity: 1;
        }
    }

    &--menu{
        .cursor__bg{
            background-color: color(orange);
        }
    }

    &--menuItem{
        @extend .cursor--big;

        .cursor__bg{
            background-color: color(orange);
            opacity: 0;
        }

        .cursor__bg--screen{
            opacity: 1;
        }
    }

    &--menu.cursor--focused{
        .cursor__bg{
            opacity: 0;
        }

        .cursor__bg--screen{
            opacity: 1;
        }
    }

    &--service{
        @extend .cursor--big;
        .cursor__bg{ 
            opacity: 0;
        }

        .cursor__bg--screen{
            opacity: 1;
        }

        
        &:not(.cursor--dark){
            .cursor__bg{ 
                opacity: 0;
            }
    
            .cursor__bg--screen{
                opacity: 0;
            }

            .cursor__bg--multiply{
                opacity: 1;
            }
        }
        
    }


    &--below{
        .cursor__bg{ 
            z-index: 10;
            background-color: color(blue);
            opacity: 1;
            mix-blend-mode: normal;
        }
        .cursor__bg--lighten{
        }
    }


    &--hidden{
        .cursor__bg{
            opacity: 0;
        }
    }

    &--medium{
        .cursor__bg{
            transform: translate3d(var(--x,50vw),var(--y,50vh),0) scale(#{(72 / 36)});
        }
    }
    &--big{
        .cursor__bg{
            transform: translate3d(var(--x,50vw),var(--y,50vh),0) scale(#{(147 / 36)});
        }
    }

    &--huge{
        .cursor__bg{
            transform: translate3d(var(--x,50vw),var(--y,50vh),0) scale(#{(266 / 36)});
        }
    }

    &--offScreen {
        .cursor__bg{
            opacity: 0 !important;
            transform: translate3d(var(--x,50vw),var(--y,50vh),0) scale(#{(1 / 36)}) !important;
        }
    }

    @media (any-pointer: coarse) {
        display: none !important;
    }
}