@font-face {
  font-family: 'icomoon';
  src: url('../fonts/icomoon.eot');
  src: url('../fonts/icomoon.eot#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf') format('truetype'),
    url('../fonts/icomoon.woff') format('woff'),
    url('../fonts/icomoon.svg#icomoon') format('svg');
  font-weight: normal;
  font-display: block;
  font-style: normal;
}


@font-face {
  font-family: 'Cambon';
  src: url('../fonts/Cambon-Bold.woff2') format('woff2'),
      url('../fonts/Cambon-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Cambon';
  src: url('../fonts/Cambon-SemiBold.woff2') format('woff2'),
      url('./fonts/Cambon-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Cambon';
  src: url('../fonts/Cambon-Regular.woff2') format('woff2'),
      url('../fonts/Cambon-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}




/* latin-ext */
@font-face {
  font-family: 'dm';
  font-style: italic;
  font-weight: 400;
  font-display: swap;
  src: url('../fonts/dm-sans-1.woff2') format('woff2');
  unicode-range: U+0100-02AF, U+0304, U+0308, U+0329, U+1E00-1E9F, U+1EF2-1EFF, U+2020, U+20A0-20AB, U+20AD-20CF, U+2113, U+2C60-2C7F, U+A720-A7FF;
}

