.homeService{
    background-color: color(dark); 
    padding-top: s(119);
    padding-bottom: s(177);
    overflow: hidden;

    *{
        user-select: none;
    }

    &__triggerStart,
    &__triggerEnd{
        position: absolute;
        top: 50%;
        height: 1px;
        left: 0;
        right: 0;
        pointer-events: none;
    }

    &__cardContainer{
        position: relative;
    }
    &__triggerStart{
        margin-top: -50vh;
    }

    &__triggerEnd{
        margin-top: 50vh;
    }

    &__inside{
        display: flex;
        flex-direction: column;
    }

    &__text{
        margin-bottom: s(93);
        width: col(7,12);
        font-family: 'dm', sans-serif;
        color: color(sand);
        font-size: s(30);
        font-weight: 400;
        line-height: 1.3em;

    }

    &__groupSlide{
        width: 9999px;
        @include clearfix();
        
    }

    &__realSize{
        float: left;
        @include clearfix();
        
    }

    &__cardSlide{
        float: left;
        perspective: 1500px;

        & + &{
            margin-left: s(35);
        }
    }

    .btn{
        margin-top: s(141);
        align-self: center;
    }

    @media screen and (max-width: 1100px) {
        padding-bottom: s(130);
        .btn{
            margin-top: s(100);
        }
        &__text{
            width: col(9,12);
            font-size: s(25);
        }
    }

    @media screen and (max-width: 600px) {
        padding-bottom: s(95);
        padding-top: s(84);

        &__text{
            width: 100%;
            font-size: s(22);
            margin-bottom: 0;
        }

        &__groupSlide{
            width: s(295);
            width: 100vw;
            padding-left: s(15);
            margin-left: s(-15);
            padding-top: s(50);
        }

        &__cardSlide{
            float: left;
            width: auto;
            perspective: 1500px;
            transform: translate3d(0,0,0) rotate(0);

            transition: 200ms transform easing(ease-out-quad);
    
            & + &{
                margin-left:0;
            }

            &.swiper-slide-next{
                transform: translate3d(0,-15px,0) rotate(2deg);
            }
        }

        .btn{
            margin-top: s(50);
        }
        
    }
}